#cubemap {
    width: 800px;
    height: 600px;
    position: relative;
    margin-top: 15px;
}

.dropzone-area {
    border: 2px dashed #cccccc;
    padding: 20px;
    text-align: center;
    transition: background-color 0.3s; /* Add this line for smooth color transition */
}

.dropzone-area.dragging {
    background-color: #f0f0f0; /* Change this to any color you prefer */
}

.hidden-file-input {
    display: none;
}



.error-message {
    color: red;
}

.bg-black {
    background-color: #151515 !important; 
}


.language-link {
    color: white;
    border: 1px solid white;
    padding: 5px;
    text-decoration: none;
    cursor: pointer;
    margin-right: 5px;  /* Optional: to add some space between the links */
}

.language-link:hover {
    color: black;
    background-color: white;
}